export default {
  name: 'FadeInUp',
  props: {
    duration: {
      type: String,
      default: '1s',
    },
    delay: {
      type: String,
      default: '0s',
    },
  },
  data() {
    return {
      startAnimate: false,
    };
  },
  mounted() {
    // 當畫面初次渲染完後（LCP 已記錄），再觸發動畫
    requestAnimationFrame(() => {
      requestAnimationFrame(() => {
        this.startAnimate = true;
      });
    });
  },
};
